.header{ 
   padding-top: 40px;
    min-height: 230px;
    // background-color: rgba(0, 0, 0, 0.56);
  
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;  
    @media screen and (min-width: 768px) {
        min-height: 216px;
      }
   
  }


.header__mylib-container{
  background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
      url(/src/images/bg-mylib-mb.jpg);

  @media screen and (min-with: 768px){
    background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
        url(/src/images/bg-mylib-tab.jpg);

        @media (min-device-pixel-ratio: 2),
          (min-resolution: 192dpi),
          (min-resolution: 2dppx) {
            background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)), url(/src/images/bg-mylib-tab-2x.jpg);
          }
  }
    @media screen and (min-with: 1024px){
      background-image: linear-gradient(rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)),
          url(/src/images/bg-mylib-desk.jpg);

          @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
              background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)), url(/src/images/bg-mylib-desk-2x.jpg);
            }
    }
}

.header_home{
  
background-position: 50% bottom  ;
  
 background-image: linear-gradient(to right,
      rgba(0, 0, 0, 0.56),
      rgba(0, 0, 0, 0.56)),
    url(/src/images/home-header-mob.jpg);

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: linear-gradient(to right,
        rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)),
      url(/src/images/home-header-mob-2x.jpg);
  }
     @media screen and (min-width: 768px) {
      background-image: linear-gradient(
        to right,
      rgba(0, 0, 0, 0.56),
        rgba(0, 0, 0, 0.56)
      ),
      url(/src/images/home-header-tab-2x.jpg);
      
      background-position: 50% 50%;
   

      @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
       background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0.56),
          rgba(0, 0, 0, 0.56)
        ),
        url(/src/images/home-header-tab-2x.jpg);
     }      
    }
         @media screen and (min-width: 1280px){
          background-image: linear-gradient(to right,
                rgba(0, 0, 0, 0.56),
                rgba(0, 0, 0, 0.56)),
              url(/src/images/home-header-desk.jpg);
            background-size: cover;
            background-position: 50% 50%;
            background-repeat: no-repeat;
          
            @media (min-device-pixel-ratio: 2),
            (min-resolution: 192dpi),
            (min-resolution: 2dppx) {
              background-image: linear-gradient(to right,
                  rgba(0, 0, 0, 0.56),
                  rgba(0, 0, 0, 0.56)),
                url(/src/images/home-header-desk-2x.jpg);
            }
        }
        
}

.nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 54px; 
  @media screen and (min-width: 768px) {
      margin-bottom: 40px;
    }
    
}
.logo_link{
  cursor: pointer;
  align-items: center;
  display: flex;
}
.logo_text {
  margin-left: 8px;
  font-weight: 500;
  font-size: 30px;
  line-height: 1.16667;
  color: $title-color;

  @media screen and (max-width: 767px) {
    display: none;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 10px;
  }

}
.menu{  
display: flex;
flex-direction: row;
gap: 40px;
@media screen and (min-width: 1280px) {
    gap: 39px;
  }
}
.menu_item {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  color: $title-color;
}

.menu_link {
  text-transform: uppercase;
  cursor: pointer;
}
.current::after {
  display: block;
  content: '';
  width: 100%;
  height: 3px;
  background-color: $notification-color;
}

.search{
position: relative;
margin-right: auto;
margin-left: auto;
width: 280px;
@media screen and (min-width: 768px) {
    width: 336px;
  }

  @media screen and (min-width: 1280px) {
   width: 394px;
  }

}

.search_input{
  width: 100%;
  background: transparent;
  outline: none;
  border: none;
  border-bottom: 0.5px solid $title-color;
  font-size: 14px;
  color:$title-color;
  line-height: 1.14;
  padding: 4px 0;
 
  &::placeholder{
    font-size: 14px;
    line-height: 1.14;
    color: $title-color;
  }
  &:focus{
    border-color: $accent-color;
      border-width: 1px;
  }
}


.search_icon{
  position: absolute;
 right: 0;
 bottom: 9px;  
}

